.page-slide {
    &-enter-active, &-leave-active {
        transition: opacity .2s ease, transform .2s ease;
    }

    &-leave-to {
        opacity: 0;
        transform: translateX(-50px);
    }

    &-enter {
        opacity: 0;
        transform: translateX(50px);
    }
}

.fade-in {
    &-enter {
        opacity: 0;
    }
}


